<template>
  <div class="articleDetail container">
    <div class="nav-bread">{{ $t("navBread.des") }}: > <router-link to="/"> {{ $t("navBread.index") }} </router-link> > <router-link to="/trade"> {{ $t("navBread.fabiTrade") }} </router-link> > <router-link to="/">  {{ $t("navBread.announcement") }} </router-link></div>
    <div class="nd-content container">
      <h4 id="nd-title">{{ detail.title }}</h4>
      <p id="nd-link">{{ $t("announcement.link") }}： {{ detail.source }} <span class="time" id="nd-time">{{ detail.create_time }}</span></p>
      <div class="nd-content-detail user-proto" id="user-proto" v-html="detail.content">
      </div>
    </div>
  </div>
</template>

<script>
import { getArticleById } from '@/api/notice'
export default {
  name: 'articleDetail',
  data() {
    return {
      detail: {}
    }
  },
  mounted() {
    this.initProto()
  },
  methods: {
    initProto() {
      const data = {}
      const id = this.$route.query.id
      data.id = id
      getArticleById(data).then(res => {
        console.log(res)
        if (res.status !== 1000) {
          this.$message.error(res.msg)
          return false
        }
        this.detail = { ...res.data }
      }).catch(() => {
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .articleDetail{
    .nd-content {
      text-align: center;
      color: #787878;
      padding: 60px 0;
      background: white;
      font-size: 13px;
      h4 {
        font-size: 16px;
        color: #3e4558;
        font-weight: normal;
      }
      .user-proto {
        margin: 28px 0px;
      }
      #nd-link{
        padding: 10px 0;
      }
      .nd-content-detail {
        padding: 13px 0;
        margin: 0 17px;
        text-align: left;
        font-size: 12px;
        line-height: 26px;
        border-top: 1px #ebecf1 dashed;
      }
    }
  }
</style>


